.modalOpen, .modalClose {
  position: fixed;
  top: 0; left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modalOpen {
        display: flex;
}
.modalClose {
    display: none;
}
.modal {
      background: white;
  padding: 2rem;
  border-radius: 10px;
  position: relative;
  min-width: 300px;
  animation: fadeIn 0.3s ease-in-out;
}
.btn {
      position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  background: transparent;
  font-size: 1.2rem;
  font-size: 24px;
  cursor: pointer;
  transform: rotate(45deg);
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-20px); }
  to { opacity: 1; transform: translateY(0); }
}